@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
*,*:before,*:after{box-sizing:border-box}body{font-family:"Inter",-apple-system,BlinkMacSystemFont,"Segoe UI","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;font-weight:500;font-size:32px;line-height:1.4;color:#1c1c21;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;margin:0}input,button,textarea,select{resize:none;border-radius:0;font-family:inherit;line-height:1.4}button{cursor:pointer;font-family:inherit}h1,h2,h3,h4{font-weight:600}a{transition:color .2s}ul{padding:0}ul li{list-style:none}p{margin:0;padding:0}figure{margin:0;padding:0}img{max-width:100%}
.error-message{display:flex;color:#ce1126;font-size:16px;font-weight:300}
.user{margin:20px 0;border-radius:6px;width:calc(33.3% - 2 * 20px);min-width:280px;min-height:560px;overflow:hidden;box-shadow:0 4px 8px rgba(0,0,0,.05),0 0 0 1px #ddd inset;position:relative;display:flex;flex-direction:column}@media only screen and (max-width: 1279px){.user{width:calc(50% - 2 * 16px);margin:20px 0}}@media only screen and (max-width: 959px){.user{width:100%;margin:16px auto}}.user__avatar{width:50px;height:50px;border-radius:50%;overflow:hidden;font-size:0;margin-right:12px}.user__header{display:flex;flex-wrap:wrap;align-items:center;padding:20px;background:linear-gradient(to bottom, rgba(0, 0, 0, 0.05) 0%, rgba(0, 0, 0, 0.05) 1%, rgba(0, 0, 0, 0) 100%)}.user__data{flex:1 1;white-space:nowrap;text-align:right;overflow:hidden;text-overflow:ellipsis;padding-left:20px}.user__info{padding:16px 20px;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;font-size:16px;line-height:1.2}.user__info:first-of-type{padding-top:48px}.user__info+.user__info{border-top:1px solid #efefef}.user__title{font-size:16px;font-weight:bold;color:rgba(0,0,0,.4);letter-spacing:1px}.user__btn--edit{border:1px solid rgba(0,0,0,.05);background-color:#ddd;border-radius:6px;padding:8px 12px;color:#1c1c21;font-size:16px;margin:auto 20px 16px;transition:all .2s ease}.user__btn--edit:hover{box-shadow:0 5px 10px rgba(0,0,0,.05)}
.user__btn--cancel{border:1px solid rgba(0,0,0,.4);background-color:rgba(0,0,0,.05);color:#ddd}.user__btn--create,.user__btn--save{border:1px solid rgba(0,0,0,.05);background-color:#ddd}.user__btn--create:hover,.user__btn--save:hover{background-color:#d0d0d0}.user__btn--cancel,.user__btn--save,.user__btn--create{border-radius:6px;padding:8px 12px;color:#1c1c21;font-size:16px;margin:16px 20px;transition:all .2s ease}.user__btn--cancel:hover,.user__btn--save:hover,.user__btn--create:hover{box-shadow:0 5px 10px rgba(0,0,0,.05)}.user__form__info:first-of-type{padding-top:0}.user__form .user__data{margin:12px 0 12px 16px;padding:8px 12px;border-radius:6px;font-size:16px;border:1px solid rgba(0,0,0,.4);text-align:start;display:inline-flex;width:100%}.user__form .user__title{display:inline-flex;align-items:center}@media only screen and (max-width: 959px){.user__form .user__title{width:100%}}.user__form .user__info{padding:8px 16px}.user__form--main .user__info{flex-direction:column;justify-content:start;align-items:start}.user__form--main .user__data{display:inline-flex;margin:12px 0 12px 24px;flex:1 0}@media only screen and (max-width: 959px){.user__form--main .user__data{display:flex;margin:12px 0 12px 16px}}.user__form--main .user__title{display:flex;align-items:baseline}.user:nth-child(3n+2){margin-left:48px;margin-right:48px}@media only screen and (max-width: 1279px){.user:nth-child(3n+2){margin-left:0;margin-right:0}}@media only screen and (max-width: 1279px){.user:nth-child(even){margin-left:auto}}
.filter{display:flex;justify-content:center}.filter__input{width:100%;background:#efefef;border:none;font-size:20px;padding:16px 12px;margin-bottom:24px}
.list{display:flex;flex-wrap:wrap;justify-content:flex-start;margin:0 auto}.pagination{margin:24px 0;display:flex;justify-content:center}
.header{display:flex;justify-content:center}.header__title{font-size:56px;line-height:1.2;color:#1c1c21}
.footer{display:flex;justify-content:center}.footer__text{color:rgba(0,0,0,.4);font-weight:300;font-size:20px;line-height:1.4;padding:16px 0}
.App{max-width:80vw;margin:0 auto}@media only screen and (max-width: 1279px){.App{max-width:60vw}}@media only screen and (max-width: 599px){.App{max-width:80vw}}
